}

form select {
  width: 200px;
}

form textarea {
  height: 90px;
  resize: none;
}

form input[type="range"] {
  appearance: none;
  width: 200px;
  background: none;
  border: none;
  padding: 0;
}

form input[type="range"]::-webkit-slider-runnable-track {
  background: var(--lightgray);
  height: 5px;
  border-radius: 5px;
}

