  display: grid;
}

.form-button {
  height: 60px;
  cursor: pointer;
  border: none;
  border-radius: 20px;
  font-family: Jost, Roboto, sans-serif;
  font-size: 1.5rem;
  transition: all .3s ease-in-out;
}

.form-button.save {
